<template>
  <div class="full">
    <Navbar v-on:update="refreshData($event)" :timestamp="timestamp" msg="ivizri.com" />
    <div class="container-fluid">
      <div class="row min-vh-50 g-0 d-flex h-100 ravel">
        <div class="col-md-7 p-3 bg-secondary">
          <div class="row">
            <stat-card
              dictKey="number_outages"
              msg="Current Outages"
              :total-data="totalData"
            />
            <stat-card
              dictKey="customers_affected"
              msg="Customers Affected"
              :total-data="totalData"
            />
          </div>
          <div class="row">
            <div class="col p-3 w-100">
              <div class="card card-body bg-dark">
                <div class="col" id="chart">
                  <AreaChart :areaData="chartData" :areaLabels="chartLabels" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-5 p-3 d-none d-md-block bg-secondary">
          <Map :map-data="mapData" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "./Navbar.vue"
import StatCard from "./StatCard.vue"
import AreaChart from "./AreaChart.vue"
import Map from "./Map.vue"

export default {
  components: { Navbar, StatCard, AreaChart, Map },
  name: "Layout",
  data() {
    return {
      requestData: false,
      chartData: [],
      chartLabels: [],
      mapData: [],
      totalData: [],
      timestamp: 0
    }
  },
  async beforeMount() {
    this.getTimelineData(false)
    this.getMapData()
  },
  methods: {
    refreshData: function (refreshData) {
      this.requestData = refreshData
      this.getTimelineData(this.requestData)
      this.getMapData(this.requestData)
      this.requestData = false
    },
    getTimelineData: function (update) {
      fetch("https://ri-outages-api.s3.amazonaws.com/data/timeseries/total.json")
        .then((res) => res.json())
        .then((data) => {
          if (update == true) {
            var newData = data.data[data.data.length - 1]
            console.log(this.timestamp)

            if (! this.chartLabels.includes(newData.timestamp)) {
              this.timestamp = newData.timestamp
              this.chartData.push(newData.customers_affected)
              this.chartLabels.push(newData.timestamp)
            } else {
              console.log("no new data to add")
            }
          } else {
            data.data.forEach((el) => {
              this.chartData.push(el.customers_affected)
              this.chartLabels.push(el.timestamp)
            })
          }
        })
    },
    getMapData: function () {
      fetch("https://ri-outages-api.s3.amazonaws.com/data/latest/map.json")
        .then((res) => res.json())
        .then((data) => {
          var total = data.pop()
          this.timestamp = total.timestamp
          this.totalData = total
          this.mapData = data
        })
    },
  },
}
</script>

<style scoped>
#chart {
  position: relative;
  margin: auto;
  height: 100%;
  width: 100%;
}

.full {
  height: 100%;
  width: 100%;
}

</style>

